@font-face {
    font-family: 'digital-clock-font';
    src: url('../font.ttf');
}

@media (max-width: 400px) {
    .BigFlexContainer {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }

    .BigFlexContainer .background {
        display: none;
    }

    .BigFlexContainer .flexRightContainer {
        display: flex;
        flex-direction: column;

    }

    .BigFlexContainer .flexRightContainer .containerTitle {
        position: relative;
        left: 55%;
        /* left: 140%; */
    }

    .BigFlexContainer .flexRightContainer .containerTitle .titlePage {
        font-family: 'digital-clock-font';
        color: #B453A4;
        font-size: 5vw;
        font-weight: '300';
        position: relative;
    }
}


@media (min-width: 401px) and (max-width: 768px){
    .BigFlexContainer {
        display: flex;
        flex-direction: row;
        
    }

    .BigFlexContainer .background{
        display: none;
    }

    .BigFlexContainer .flexRightContainer {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

    }

    .BigFlexContainer .flexRightContainer .containerTitle {
        position: relative;
        left:25%;
        margin-bottom:-4.0%;
        /* left: 140%; */
    }

    .BigFlexContainer .flexRightContainer .containerTitle .titlePage {
        font-family: 'digital-clock-font';
        color: #B453A4;
        font-size: 5vw;
        font-weight: '300';
        position: relative;
    }
}

@media (min-width: 769px) and (max-width: 1024px) {
    .BigFlexContainer {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
    }


    .BigFlexContainer .background {
        display: none;
    }

    .BigFlexContainer .flexRightContainer {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }

    .BigFlexContainer .flexRightContainer .containerTitle {
        position: relative;
        left: 26%;
        margin-bottom:-4%;
       
    }

    .BigFlexContainer .flexRightContainer .containerTitle .titlePage {
        font-family: 'digital-clock-font';
        color: #B453A4;
        font-size: 3.3vw;
        font-weight: '300';
        position: relative;
    }


}

