@media (min-width: 1025px) and (max-width: 1440px) {
    .BigFlexContainer {
        display: flex;
        flex-direction: row;
        
    }

    .BigFlexContainer .background {
        position: relative;
        top: 5%;
        /* right: 4%; */
        width:60vw;
    }

    .flexBigContainer .background img {
        position: relative;
        z-index: -1;
        
    }

    #content{
        width:45vw;
        position:relative;
        right:15%;
    }


    .BigFlexContainer .flexRightContainer {
        display: flex;
        flex-direction: column;
        justify-content:center;
        align-items:center;
    }

    .BigFlexContainer .flexRightContainer .containerTitle {
        position: relative;
    }

    .BigFlexContainer .flexRightContainer .containerTitle .titlePage {
        font-family: 'digital-clock-font';
        color: #B453A4;
        font-size: 2vw;
        font-weight: '300';
        position: relative;
        bottom:80%;
        margin-bottom:-1%;
        left:20%;
    }
    #content{
        bottom:5%;
    }
}

@media (min-width: 1441px) and (max-width: 1920px){
    .BigFlexContainer {
            display: flex;
            flex-direction: row;
        }
    
    .BigFlexContainer .background {
        position: relative;
        top: 10%;
        
        /* right: 4%; */

    }

    .flexBigContainer .background img {
        position: relative;
        z-index: -1;
        

    }


    .BigFlexContainer .flexRightContainer {
        display: flex;
        flex-direction: column;
    }

    .BigFlexContainer .flexRightContainer .containerTitle {
        position: relative;
        left: 2.5%;
    }

    .BigFlexContainer .flexRightContainer .containerTitle .titlePage {
        font-family: 'digital-clock-font';
        color: #B453A4;
        font-size: 2.7vw;
        font-weight: '300';
        position: relative;
    }
}


